@import "../../sass/varibles";

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  .cards {
    background-color: $DarkGrey;
    margin: 1rem;
    padding: 1rem;
    border-radius: 10px;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.7);
    position: relative;
    overflow: hidden;
    &:hover .card-over {
      transform: translate(0%);
    }
    .title {
      color: $Pink;
      font-size: 1rem;
      padding: 1rem;
    }
  }
  img {
    width: 500px;
    height: 350px;
    object-fit: cover;
    filter: grayscale(0);
    &:hover {
      filter: grayscale(10);
    }
  }
  .card-over {
    color: $lightPink;
    position: absolute;
    bottom: 0;
    left: 0;
    font-size: 1.5rem;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 1rem;
    transform: translateY(100%);
    z-index: 1;
    transition: transform 0.7s ease-in-out;
  }
}
